/* Hero */

#hero {
    display: flex; /* Flex containerを有効にする */
    justify-content: center; /* 中央に揃える */
    align-items: center; /* アイテムを垂直方向に中央に揃える */
    width: 100%;
    max-width: 1100px; /* コンテナの最大幅を調整 */
    margin: 0 auto; /* センタリング */
}

.hero-image, .hero-desc {
flex: 1; /* 各要素が等しい幅を取るようにする */
}

.hero-image img {
width: 90%; /* 画像をコンテナに合わせて拡大/縮小 */
margin: 0;
height: auto;
display: block;
}

.hero-desc {
padding: 30px; /* テキスト周辺の余白 */
}

.hero-link {
  border-bottom: 2px solid black;
}

.hero-link:hover {
  color: gray;
}





@media (max-width: 768px) {
    #hero {
      flex-direction: column; /* 子要素を縦に並べる */
    }
  
    .hero-image {
      width: 70%; /* コンテナ全体の幅を使用 */
      display: flex; /* Flexboxを有効にする */
      justify-content: center; /* 中央揃え */
      align-items: center; /* 垂直方向の中央揃え */
      margin: 0; /* マージンをリセット */
      padding: 0; /* パディングをリセット */
    }
  
    .hero-image img {
      width: auto; /* 画像の幅を自動調整 */
      max-width: 100%; /* 画像がコンテナを超えないようにする */
      height: auto; /* 画像の高さを自動調整 */
    }
  
    .hero-desc {
      font-size: 16px; /* フォントサイズを小さくする */
      padding: 0;
    }
  }

@media (max-width: 550px) {  
  .hero-desc {
    font-size: 10px; /* フォントサイズを小さくする */
    padding: 0;
  }
}